.accordion {
  display: flex;
  flex-direction: column;
  row-gap: 9px;
  padding: 0;
  list-style-type: none;

  @include vp-1199 {
    row-gap: 10px;
  }

  @include vp-767 {
    row-gap: 9px;
  }
}

.accordion__element.is-active {
  background-color: $color-cerulean-blue;
  color: $color-default-white;
  padding-bottom: 32px;
  border-radius: 16px;

  @include vp-1199 {
    padding-bottom: 25px;
  }

  @include vp-767 {
    padding-bottom: 14px;
  }
}

.accordion__button {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 100%;
  color: $color-cerulean-blue;
  background-color: $color-alabaster;
  padding: 43px 40px 37px 40px;
  border: 1px solid $color-cerulean-blue;
  border-radius: 16px;
  cursor: pointer;

  &:hover,
  &:focus-visible {
    background-color: $color-default-white;
    outline: none;

    & span:nth-child(2n) {
      background-color: $color-default-white;
      border: 1px solid $color-cerulean-blue;

      &::before {
        background-color: $color-cerulean-blue;
      }
    }
  }

  @include vp-1199 {
    align-items: center;
    padding: 30px 29px 29px 29px;
  }

  @include vp-767 {
    padding: 20px 15px 19px 15px;
  }

  &:active {
    background-color: $color-default-white;
    color: $color-endeavour;

    & span:nth-child(2n) {
      background-color: $color-endeavour;
      border: none;

      &::before {
        background-color: $color-default-white;
      }
    }
  }
}

.accordion__button span:nth-child(1n) {
  margin: 0;
  padding: 0;
  width: 72%;
  font-size: 28px;
  font-weight: 400;
  line-height: 34px;
  text-align: start;

  @media (min-width: 1200px) and (max-width: 1440px) {
    font-size: calc(26px + (28 - 26) * ( (100vw - 1200px) / ( 1440 - 1200) ));
  }

  @include vp-1199 {
    max-width: 500px;
    font-size: 22px;
    line-height: 26px;
    width: 80%;
  }

  @include vp-767 {
    width: 88%;
    font-size: 16px;
    line-height: 18px;
  }
}

.accordion__span br {
  @include vp-767 {
    display: none;
  }
}

.accordion__button span:nth-child(2n) {
  position: relative;
  margin: 0;
  padding: 0;
  max-width: 40px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $color-cerulean-blue;

  &::before {
    position: absolute;
    content: "";
    mask-image: url("../img/sprite/plus.svg");
    width: 20px;
    height: 20px;
    background-color: $color-default-white;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }

  @include vp-1199 {
    max-width: 40px;
    width: 40px;
  }


  @include vp-767 {
    max-width: 40px;
    width: 40px;
  }
}

.is-active .accordion__button {
  padding-bottom: 0;
  background-color: $color-cerulean-blue;
  color: $color-default-white;

  &:hover,
  &:focus-visible {
    & .accordion__button-icon {
      background-color: $color-cerulean-blue;
      border: 1px solid $color-default-white;

      &::before {
        background-color: $color-default-white;
      }
    }
  }

  &:active {
    background-color: $color-endeavour;
    color: $color-default-white;
    border: none;

    & .accordion__button-icon {
      background-color: $color-default-white;
      border: none;

      &::before {
        background-color: $color-endeavour;
      }
    }
  }
}

.accordion__element.is-active:has(.accordion__button:active) {
  background-color: $color-endeavour;
}

.is-active .accordion__button span:nth-child(2n) {
  position: relative;
  background-color: $color-default-white;

  &::before {
    position: absolute;
    content: "";
    mask-image: url("../img/sprite/minus.svg");
    width: 20px;
    height: 20px;
    background-color: $color-cerulean-blue;
    background-repeat: no-repeat;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
}

.accordion__content {
  width: 100%;
  max-width: 725px;
  padding: 0 40px 0 40px;

  @include vp-1199 {
    width: 87%;
    max-width: unset;
    padding: 0 29px 0 29px;
  }

  @include vp-767 {
    width: 100%;
    padding: 0 15px 0 15px;
  }
}

.accordion__content p {
  margin: 10px 0 0 0;
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;

  @include vp-1199 {
    line-height: 20px;
    margin-top: 15px;
  }

  @include vp-767 {
    line-height: 20px;
    font-size: 14px;
  }
}

.accordion__button--fourth {
  padding-top: 34px;
  padding-bottom: 33px;

  @include vp-1199 {
    padding-top: 25px;
    padding-bottom: 25px;
  }

  @include vp-767 {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.accordion__button--fifth {
  padding-top: 30px;

  @include vp-1199 {
    padding-top: 24px;
    padding-bottom: 25px;
  }

  @include vp-767 {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.accordion__button--fourth .accordion__button-icon,
.accordion__button--fifth .accordion__button-icon {
  margin-top: 10px;

  @include vp-1199 {
    margin-top: 0;
  }
}
